@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 13px;
  font-family: "Epilogue", sans-serif;
  background: hsl(0deg, 0%, 98%);
  height: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  list-style-type: none;
}

.container {
  max-width: 1200px;
}

.cta-btn {
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  width: 32px;
  height: 32px;
  outline: none;
  border: none;
  cursor: pointer;
}
.cta-btn:hover, .cta-btn:focus {
  outline: 1px solid grey;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 20, 20, 0.8);
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  transition: all 500ms ease-in-out;
}
.overlay.activated {
  visibility: visible;
  opacity: 1;
}

.header {
  padding: 24px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
}
@media (min-width: 40em) {
  .header {
    justify-content: unset;
  }
  .header button {
    display: none;
  }
}

.header__cta--open {
  background-image: url("../../images/icon-menu.svg");
}

.header__cta--close {
  background-image: url("../../images/icon-close-menu.svg");
}

.header__navbar {
  position: absolute;
  top: 0;
  right: 0;
  background: hsl(0deg, 0%, 98%);
  width: 40%;
  height: 100vh;
  padding: 24px 16px;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  transition: all 500ms ease-in-out;
  z-index: 999;
}
@media (min-width: 40em) {
  .header__navbar {
    position: initial;
    height: unset;
    width: 100%;
    visibility: visible;
  }
}
.header__navbar button {
  align-self: flex-end;
}
.header__navbar.slide-in {
  visibility: visible;
  opacity: 1;
  width: 65%;
}
.header__navbar.slide-in .dropdown.active .submenu__container {
  visibility: initial;
}

.navbar__menu {
  padding: 35px 0;
}
@media (min-width: 40em) {
  .navbar__menu {
    padding: unset;
    display: flex;
    align-items: center;
  }
}
.navbar__menu .dropdown.active .submenu:after {
  transform: rotate(0);
}
.navbar__menu .dropdown.active .submenu__container {
  opacity: 1;
  max-height: 999px;
  position: inherit;
}
@media (min-width: 40em) {
  .navbar__menu .dropdown.active .submenu__container {
    position: absolute;
    right: 0;
    top: 30px;
    opacity: 1;
    visibility: visible;
    max-height: unset;
    background: hsl(0deg, 0%, 98%);
    border-radius: 8px;
    -webkit-box-shadow: 0px 0px 15px -3px rgba(3, 129, 166, 0.8);
    box-shadow: 0px 0px 15px -3px rgba(3, 129, 166, 0.8);
    padding: 16px;
  }
}
.navbar__menu .dropdown .submenu:after {
  display: inline-block;
  content: url(" ../../images/icon-arrow-up.svg ");
  margin-left: 10px;
  transform: rotate(180deg);
  transition: transform 300ms ease-in-out;
}
.navbar__menu .dropdown .submenu__container {
  max-height: 1px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 20px 0 5px 20px;
  opacity: 0;
  position: absolute;
  visibility: hidden;
  transition: max-height 350ms ease-in-out, opacity 350ms ease-in-out;
  right: 0;
}
.navbar__menu .dropdown .submenu__container li {
  width: max-content;
}
.navbar__menu .dropdown .submenu__container li:not(:last-child) {
  margin-bottom: 20px;
}
.navbar__menu .dropdown .submenu__container a {
  color: hsl(0deg, 0%, 41%);
  text-transform: capitalize;
  display: flex;
  align-items: flex-end;
}
.navbar__menu .dropdown .submenu__container a.feature::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 15px;
  background-position: center;
  background-repeat: no-repeat;
}
.navbar__menu .dropdown .submenu__container a.todo::before {
  background-image: url("../../images/icon-todo.svg");
}
.navbar__menu .dropdown .submenu__container a.calendar::before {
  background-image: url("../../images/icon-calendar.svg");
}
.navbar__menu .dropdown .submenu__container a.reminders::before {
  background-image: url("../../images/icon-reminders.svg");
}
.navbar__menu .dropdown .submenu__container a.planning::before {
  background-image: url("../../images/icon-planning.svg");
}
.navbar__menu .navbar__item {
  position: relative;
  padding-bottom: 20px;
}
@media (min-width: 40em) {
  .navbar__menu .navbar__item {
    padding-bottom: unset;
    padding-left: 40px;
  }
}
.navbar__menu .navbar__item .navbar__link {
  color: hsl(0deg, 0%, 41%);
  text-transform: capitalize;
}
.navbar__menu .btn-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 40em) {
  .navbar__menu .btn-container {
    flex-direction: row;
    margin-left: auto;
  }
}
.navbar__menu .btn-container .btn {
  display: block;
  padding: 10px 0;
  text-align: center;
  border-radius: 10px;
  font-size: 13px;
  color: hsl(0deg, 0%, 41%);
}
@media (min-width: 40em) {
  .navbar__menu .btn-container .btn {
    padding: 10px 20px;
  }
}
.navbar__menu .btn-container .btn-register {
  width: 100%;
  margin-top: 10px;
  border: 2px solid hsl(0deg, 0%, 41%);
}
@media (min-width: 40em) {
  .navbar__menu .btn-container .btn-register {
    margin-top: unset;
  }
}

.main {
  text-align: center;
}
.main .hero {
  width: 100%;
}
.main .hero img {
  width: 100%;
}
.main__wrapper {
  padding: 24px 16px;
}
.main__wrapper .title {
  font-size: 32px;
  text-transform: capitalize;
  margin-top: 20px;
}
.main__wrapper .description {
  line-height: 25px;
  color: hsl(0deg, 0%, 41%);
  margin: 30px 0;
  font-size: 16px;
}
.main__wrapper .description::first-letter {
  text-transform: capitalize;
}
.main__wrapper .l-btn {
  display: inline-block;
  padding: 15px 25px;
  font-size: 15px;
  color: hsl(0deg, 0%, 98%);
  background-color: hsl(0deg, 0%, 8%);
  border-radius: 10px;
}
.main__wrapper .clients__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 50px 0;
}
.main__wrapper .clients__container .client {
  width: 20%;
}
.main__wrapper .clients__container .client img {
  width: 100%;
}